body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  width: 100%;
  max-width: 700px;
  margin: 0px auto;

}
.home{
    width: 100%;
  max-width: 700px;
  margin: 0px auto;
}
h1 {
  text-align: center;
  margin-bottom: 0px;
}
h2{
  color: crimson;
}
label{
color: gray;
}
p {
  font-size: 18px;
  text-align: center;
  margin-left: 52px;
}
div.box {
  display: flex;
  justify-content: center;
}

.line {
  width: 36px;
  height: 3px;
  background-color: white;
  margin: 4px 0px;
}
.item {
  margin-left: 16px;
  margin-top: 14px;
  cursor: pointer;
}
.nav {
  background-color: blueviolet;
  color: white;
  margin-top: 0px;
  padding: 4px 0px;
}
.book{
 box-shadow: 2px 2px 4px gainsboro;
  padding: 6px 28px;
  margin: 10px 0px;
}
.slide{
  border-style: none;
  max-width: 700px;
  position: absolute;
  top: 83px;
  right: 0px;
  color: white;
  height: 510px;
  /* width: 0px; */
  width: 0px; 
  z-index: 999;
  background-color:ghostwhite;
  transition: all 0.2s;
  box-shadow: 2px 2px  8px gray;
  border-radius: 40px 0px 90px 0px;
}

h4{
  color:dodgerblue;
  text-align: center;
  font-size: 18px;
  margin: 18px 0px;
}

input[type="text"]{
  display: block;
  margin: 14px auto;
  width: 86%;
  height: 24px;
  padding-left: 12px;
}

input[type="submit"]{
  display: block;
  margin: 16px auto;
  padding: 6px 6px;
  width: 200px;
  border-radius: 16px;
  border-style: hidden;
  background-color: crimson;
  color: white;
  font-size: 16px;
  transition: all 0.2s;

}
.del{
  border-radius: 12px;
  padding: 4px 10px;
  background-color: dodgerblue;
  border-style: none;
  color: white;
position: absolute;
right: 35px;
font-size: 18px;
margin-top: -6px;
}

span{
  color:black;
  font-weight: bold;
  display: block;
  margin-left: 20px;
  margin-top: 20px;
  font-size: 20px;
  margin-bottom: -10px;
 
}

.btn:active{
  background-color: green;
  transform: scale(1.1);

}
.del:active{
  background-color: red;
}

.message{
  font-size: 20px;
  text-align: center;
  margin-top: 14px;
  color:red;
}
span:active{
  color: red;
}